<template>
    <div class="container min-vh-100">
      <div class="row">
          <div class="col-md-3">
            <div class="text-center mb-3 mb-md-0" style="position:sticky;top:80px;">
              <span class="h4 d-none d-md-inline"></span>
              <div class="list-group list-group-flush mt-md-3 mt-0">
                <a href="#" class="list-group-item list-group-item-action list-group-item-light" :class="{'active':activeItem.active === ''}" @click.prevent="changeCategory('')"></a>
                <a href="#" class="list-group-item list-group-item-action list-group-item-light" :class="{'active':activeItem.active === ''}" @click.prevent="changeCategory('')"></a>
                <a href="#" class="list-group-item list-group-item-action list-group-item-light" :class="{'active':activeItem.active === ''}" @click.prevent="changeCategory('')"></a>
                <a href="#" class="list-group-item list-group-item-action list-group-item-light" :class="{'active':activeItem.active === ''}" @click.prevent="changeCategory('')"></a>
                <a href="#" class="list-group-item list-group-item-action list-group-item-light" :class="{'active':activeItem.active === ''}" @click.prevent="changeCategory('')"></a>
              </div>
            </div>
          </div>
          <div class="col-md-9">
            <div class="text-center mb-3">
              <span class="h4 d-inline text-secondary">{{nowCategoryTilte}}</span>
            </div>
            <ProductList :getProducts="nowCategory"></ProductList>
          </div>
      </div>
    </div>
</template>

<script>
import ProductList from './MainProductsList'
export default {
  components: {
    ProductList
  },
  data () {
    return {
      allProducts: [],
      nowCategory: [],
      nowCategoryTilte: '',
      activeItem: {
        active: ''
      }
    }
  },
  methods: {
    getProducts () {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products/all`
      const vm = this
      vm.$bus.$emit('loading: push', 'start')
      this.$http.get(api).then((response) => {
        vm.allProducts = response.data.products
        vm.nowCategory = vm.allProducts
        vm.nowCategoryTilte = ''
        vm.activeItem.active = ''
        vm.$bus.$emit('loading: push', 'stop')
      })
    },
    changeCategory (category) {
      const vm = this
      vm.nowCategoryTilte = category
      if (category === '') {
        vm.nowCategory = vm.allProducts
        vm.activeItem.active = ''
      } else if (category !== '') {
        vm.nowCategory = vm.allProducts.filter((item) => {
          return item.category === category
        })
        vm.activeItem.active = category
      }
    }
  },
  created () {
    const vm = this
    vm.getProducts()
  }
}
</script>

<style lang="scss" scoped>
     @import url("@fortawesome/fontawesome-free/css/all.css");
     .container{
         padding-top: 70px;
     }
     .pointer{
         &:hover{
             cursor: pointer;
         }
     }
</style>



